	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Beach Culture","date":"01 Aug 2004","slug":"/2004-08-01-beach-culture/","tags":"Y2004"},"html":"<p>We're back, none the worse for wear. And we noticed some huge differences between the German island of Sylt and the Danish island of Rømø. On Sylt, the beaches are heavily regulated and environmentally protected. A fee is required for parking and for entry, the beach is cleaned daily, covered beach chairs <em>(Strandkörbe)</em> are available for rent, food and drink are available for sale, and certain fringe groups (namely nudists and dogs) are given their own areas as to not disturb others. None of this applies on Rømø, where you simply drive onto the beach, unpack your gear, fire up your grill, and start cooking.</p>\n<p>I wouldn't consider this all that seriously as evidence of the differences between Danes and Germans. For one thing, there are geographical differences... Sylt is eroding away, while Rømø has more sq. km of sand than land. And most of those driving and grilling on the beaches of Rømø were Germans.</p>"},"next":{"fields":{"title":"Beach Weekend","date":"30 Jul 2004","slug":"/2004-07-30-beach-weekend/"}},"previous":{"fields":{"title":"Cat and Mouse","date":"01 Aug 2004","slug":"/2004-08-01-cat-and-mouse/"}}},{"node":{"fields":{"title":"Beach Weekend","date":"30 Jul 2004","slug":"/2004-07-30-beach-weekend/","tags":"Y2004"},"html":"<p>Mama has to visit a location in <a href=\"http://www.westerland.de/\">Westerland</a> for work, so we're combining her business with our pleasure and spending the weekend on the North Sea. It's the height of the summer season, so there are no rooms left on the entire island of <a href=\"http://www.sylt.de/\">Sylt</a>, but we did find accomodation on the neighboring Danish island of <a href=\"http://www.romo.dk/\">Rømø</a>. Here's hoping we don't get stuck in traffic!</p>"},"next":{"fields":{"title":"Swag","date":"29 Jul 2004","slug":"/2004-07-29-swag/"}},"previous":{"fields":{"title":"Beach Culture","date":"01 Aug 2004","slug":"/2004-08-01-beach-culture/"}}},{"node":{"fields":{"title":"Swag","date":"29 Jul 2004","slug":"/2004-07-29-swag/","tags":"Y2004"},"html":"<p>If the convention bloggers are getting <a href=\"http://archive.scripting.com/2004/07/29#When:8:18:27AM\">swag</a> from the Democrats, does that mean the bloggers are being influenced?</p>"},"next":{"fields":{"title":"Score One for the Tagesschau","date":"28 Jul 2004","slug":"/2004-07-28-score-one-for-the-tagesschau/"}},"previous":{"fields":{"title":"Beach Weekend","date":"30 Jul 2004","slug":"/2004-07-30-beach-weekend/"}}}],"pathPrefix":"","first":false,"last":false,"index":443,"pageCount":1101}}

